.button {
    display: inline-block;
    border-radius: 50px;
    background-color: rgb(247, 157, 157);
    border: none;
    color: #FFFFFF;
    text-align: center;
    font-size: 200%;
    padding: 10px;
    width: 40%;
    transition: all 0.3s;
    cursor: pointer;
    margin: 5px;
}

.button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.3s;
}

.button span:after {
    content: '\BB';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}

.button:hover span {
    padding-right: 25px;
}

.button:hover span:after {
    opacity: 1;
    right: 0;
}

.App {
